.factsheet-edit-page {
  height: 100%;
}
.factsheet-edit-page .SplitPane {
  height: calc(100% - 50px);
}
.factsheet-edit-page .leftPannel {
  height: 100%;
  width: 100%;
  padding-top: 6px;
  margin-left: 6px;
  position: relative;
}
.factsheet-edit-page .leftlayout {
  height: 100%;
  width: 100%;
  padding-top: 16px;
  margin-left: 0px;
}
.factsheet-edit-page .SplitPane.vertical {
  height: calc(100% - 50px) !important;
}
.factsheet-edit-page .factsheetWrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 0px;
  height: 100%;
}
.factsheet-edit-page .leftlayout .storyview .GeneratingDiv {
  width: 513px;
  background: #FFFFFF;
  border: 4px solid #EBEDF2;
  box-shadow: 0 3px 10px 0 rgba(165, 165, 165, 0.5);
  border-radius: 1px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 32px 79px;
  position: relative;
}
.factsheet-edit-page .leftlayout .storyview .GeneratingDiv .closeIcon {
  position: absolute;
  right: 24px;
  top: 24px;
  font-size: 17px;
}
.factsheet-edit-page .leftlayout .storyview .GeneratingDiv .closeIcon:hover {
  color: #F28B30;
}
.factsheet-edit-page .leftlayout .storyview .GeneratingDiv .wrapper {
  position: absolute;
  right: 37px;
  height: 30px;
  margin-top: 38px;
  bottom: 29px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}
.factsheet-edit-page .leftlayout .storyview .GeneratingDiv .wrapper .centerWrapper {
  width: 80px;
  height: 30px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  color: #000000;
  letter-spacing: 0;
  text-align: center;
  border: 1px solid #484E5D;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  cursor: pointer;
}
.factsheet-edit-page .leftlayout .storyview .GeneratingDiv .wrapper .centerWrapper:hover {
  color: #F28B30;
  border: 1px solid #F28B30;
}
.factsheet-edit-page .leftlayout .storyview .GeneratingDiv .wrapper div:nth-child(1) {
  margin-right: 20px;
}
.factsheet-edit-page .leftlayout .storyview .GeneratingDiv .GeneratingTitle {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
  color: #F28B30;
  letter-spacing: 0;
}
.factsheet-edit-page .leftlayout .storyview .GeneratingDiv .GeneratingTitle .loadingImage {
  width: 25px;
  height: 25px;
  background-size: contain;
  -webkit-animation: rotating 1.2s linear infinite;
          animation: rotating 1.2s linear infinite;
}
.factsheet-edit-page .leftlayout .storyview .GeneratingDiv .GeneratingTitle .LoadingTxt {
  margin-left: 15px;
  display: block;
}
.factsheet-edit-page .leftlayout .storyview .GeneratingDiv .GeneratingTitle .dotSpan {
  display: inline-block;
  width: 1.5em;
  vertical-align: bottom;
  overflow: hidden;
  -webkit-animation: dot 2s infinite step-start;
  animation: dot 2s infinite step-start;
}
.factsheet-edit-page .leftlayout .storyview .GeneratingDiv .messageInfo {
  display: block;
  margin-top: 21px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  color: #000000;
  letter-spacing: 0;
}
.factsheet-edit-page .FactWrapper {
  margin-left: 20px;
  width: 220px;
  height: 737px;
  margin-top: 55px;
}
.factsheet-edit-page .GoalWrapper {
  width: 220px;
  height: 190px;
  margin-top: 5px;
  background-color: #FFFFFF;
  border: 1px solid #F4F4F4;
}
